export default {
  data () {
    return {
      items: [
        {
          name: 'toast',
          url: '/',
          icon1: '#icon-index1',
          icon2: '#icon-index0'
        },
        {
          name: 'dialog',
          url: '/dialog',
          icon1: '#icon-mine1',
          icon2: '#icon-mine0'
        }
      ]
    }
  },
  methods: {
    toUrl (i) {
      this.$router.replace({
        path: this.items[i].url
      })
    }
  }
}



