import footerBar from '../components/footerBar'
export default {
  components: { footerBar },
  // name: 'dialog',
  data () {
    return {
      list: [
        { myId: 1, myName: '选项一' },
        { myId: 2, myName: '选项二' },
        { myId: 3, myName: '选项三' }
      ]
    }
  },
  methods: {
    comfirm1 () {
      this.$dialog({
        title: '删除提示111',
        text: '是否确定删除此标签？',
        showCancelBtn: false,
        confirmText: '我知道了',
        confirm (content) {
          alert('删除成功')
        }
      })
    },
    comfirm2 () {
      this.$dialog({
        title: '删除提示',
        text: '是否确定删除此标签？',
        showCancelBtn: true,
        confirmText: '确认',
        confirm (content) {
          alert('删除成功')
        }
      })
    },
    feedback () {
      this.$dialog({
        title: '请填写反馈',
        text: '辅助文本',
        type: 'input',
        placeholder: '请输入反馈内容...',
        length: 10,
        showCancelBtn: true,
        confirmText: '确认',
        confirm (content) {
          alert(content)
        }
      })
    },
    toSelect () {
      this.$selectDialog.show({
        title: '流程结束',
        text: '请选择其中一项进行处理',
        showCancelBtn: true,
        confirmText: '确认',
        defaultProps: {
          name: 'myName',
          code: 'myId'
        },
        list: this.list,
        selectItem: {
          myId: 2, myName: '选项二'
        },
        confirm (content) {
          alert(content[this.defaultProps.name])
        }
      })
    }
  }
  // destroyed () {
  //   this.$dialog.hide()
  //   this.$selectDialog.hide()
  // }
}



